import { Component, OnInit, ViewEncapsulation, AfterViewInit, AfterViewChecked, ElementRef, ViewChild, HostBinding, Renderer2, Directive} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const NUM_REGEX=/[0-9]/;
const WORD_REGEX=/w{5,}/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./ticket-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class TicketAddComponent implements OnInit, AfterViewInit {
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

	ticketForm: FormGroup;
	ticketId = 0;  
	ticket = new TicketData();

	config = new configDetails();  
	public status: string; 
	 
	public msg = [];
	public category = [];
	public type = [];
	public source = [];
	public priority=[];
	public project = [];
	public executive = [];
	public allexecutive = [];
	public queue = [];
	 
	compModulePm = sessionStorage.getItem('compModulePm');
	bootstrapFormValues = [];
	public reportMsg = '';
	
constructor( private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
		this.route.queryParams.filter(params => params.ticketId).subscribe(params=>{
				if(params.ticketId != undefined){
					this.ticketId = params.ticketId; 
				}
			}); 
		if(this.ticketId == 0)
			{
			this.populateDropDownData();
			this.status = "Add";
			}
		else
		{
			this.populateData();
			this.status = "Update";	
		}
	}
	
	ngAfterViewInit(){
		  $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
		}	
	
    ngOnInit() {
			this.ticketForm = new FormGroup({	
			'ticketSubject' : new FormControl('', [ Validators.required, Validators.minLength(5)]),
			'ticketContactId' : new FormControl(''),
			'ticketReportTime' : new FormControl(''),
			'ticketRefno' : new FormControl('', [ Validators.required, Validators.pattern(NUM_REGEX)]),
			'ticketDesc' : new FormControl('', [ Validators.required]),
			'ticketTicketcatId' : new FormControl(''),
			'ticketTickettypeId' : new FormControl(''),
			'ticketTicketsourceId' : new FormControl('', [ Validators.required]),
			'ticketPriorityticketId' : new FormControl('', [ Validators.required]),
			'ticketTicketqueueId' : new FormControl('', [ Validators.required]),
			'ticketPmId' : new FormControl(''),
			'ticketEmpId': new FormControl('', [ Validators.required]),
		});
		}
	  
 
	 get ticketSubject(){ return this.ticketForm.get('ticketSubject')};
	 get ticketContactId(){ return this.ticketForm.get('ticketContactId')};
	 get ticketReportTime(){ return this.ticketForm.get('ticketReportTime')};
	 get ticketRefno(){ return this.ticketForm.get('ticketRefno')};
	 get ticketDesc(){ return this.ticketForm.get('ticketDesc')};
	 get ticketTicketcatId(){ return this.ticketForm.get('ticketTicketcatId')};
	 get ticketTickettypeId(){ return this.ticketForm.get('ticketTickettypeId')};
	 get ticketTicketsourceId(){ return this.ticketForm.get('ticketTicketsourceId')};
	 get ticketPriorityticketId(){ return this.ticketForm.get('ticketPriorityticketId')};
	 get ticketTicketqueueId(){ return this.ticketForm.get('ticketTicketqueueId')};
	 get ticketPmId(){ return this.ticketForm.get('ticketPmId')};
	 get ticketEmpId(){ return this.ticketForm.get('ticketEmpId')};
 
	 populateDropDownData(){
		  
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/populateTicketCategory',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.category = data['populateTicketCategory']
																	});
		  
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/ticket-type',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.type = data['populateTickettype']
																	});
		  
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/ticket-source',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.source = data['populateSource']
																	});
		  
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/ticket-priority',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.priority = data['populateTicketPriority']
																	});
		  
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/populateproject',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.project = data['populateProject']
																	});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/queue',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.queue = data['populateQueue']
																	});
		  
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/allexecutive',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.allexecutive = data['populateallexecutive']
																	});
		  
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/executive',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.executive = data['populateExecutive']
																	});
		  
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/serviceconfigdetails',
							 			JSON.stringify({ }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.config = data['populateConfigTicketDetails'][0];
				  														console.log(this.config);
			  															});	
	  }
	 

		populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<TicketData>(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/ticket-data',
							 			JSON.stringify({ ticketId :this.ticketId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.ticket = data['populateData'][0];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
			  														this.populateDropDownData();								
															 });
	 }

 
	
 
		 toggleAnimation(){
				   this._router.navigate(['helpdesk/ticket-list']);
		 }

 
	  	onSubmit() {
	  	  this.bootstrapFormValues = $('#m_form_1').serializeArray();
	  	  for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.ticket[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				if(this.bootstrapFormValues[i].name=="ticketReportTime"){
					if(this.bootstrapFormValues[i].value==""){
						this.reportMsg = "<font class='errorMsg'>Enter Report Time</font>";
					}else{
						this.reportMsg = "";
					}
				}
			}
				
		  if(!this.ticketForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.ticketId == 0){
			this.ticket['requestType'] = 'add';
		}else{
			this.ticket['requestType'] = 'update';
			this.ticket['ticketId'] = this.ticketId+'';
		}
			  alert(this.ticketId);
			  console.log(this.ticketForm.value);
		 this.http.post(sessionStorage.getItem('requestUrl')+"/axelacrm/helpdesk/ticket",
									JSON.stringify(this.ticket),
						{ headers, withCredentials: true }).subscribe(data => {
			 											
			 			if(data['successmsg'].includes('Successfully')){
						this.msg[0]='success';
						this.msg[1]=data['successmsg'];
						}else{
						this.msg[0]='Error';
						this.msg[1]=data['errormsg'];
						}
						this.toast.showtoast(this.msg);
						this.toggleAnimation();
						this.ticketForm.reset();
						},
						err =>{
							console.log(err['error']['desc']);
							this.msg[0]='Error';
							this.msg[1]=err['error'];
							this.toast.showtoast(this.msg);
						});
		  }
	  }
	  
	 
	 onDelete(){
		 this.ticket['requestType'] = 'delete';
		 this.ticket['ticketId'] = this.ticketId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+"/axelacrm/helpdesk/ticket",
									JSON.stringify({ requestType : 'delete',
												   'ticketId' : this.ticketId }),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.ticketForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
	 	}
	 
}

		
	
export class TicketData {
    ticketId: string;
    ticketReportTime: string = '';
	ticketTicketsourceId: string;
	ticketContactId: string;
	ticketTicketqueueId: string;
	ticketTicketcatId: string;
	ticketRefno: string;
	ticketPmId: string;
	ticketDesc: string;
	ticketTickettypeId: string;
	ticketPriorityticketId: string;
	ticketEmpId: string;
	ticketSubject: string;
	ticketEntryBy: string
	ticketEntryDate: string;
	ticketModifiedBy: string;
	ticketModifiedDate: string;
}


export class configDetails
{
	configServiceTicketCat: string;
	configServiceTicketType: string;
	configServiceTicketRefno: string;
}



