import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { LayoutModule } from '../../../layouts/layout.module';
import { DefaultComponent } from '../../default.component';
import {HttpClientModule} from '@angular/common/http';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CdkTableModule} from '@angular/cdk/table';
import { TicketListComponent } from './ticket-list.component';
import { TicketAddComponent } from './ticket-add.component';
import { TicketDashComponent } from './ticket-dash.component';
import {ListModule} from '../../list/list.module';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component';
import {ToastModule} from '../../../layouts/toast-notification/toast.module';
import { SmartSearchModule } from '../../../layouts/smart-search/smart-search.module';
import {
	  MatAutocompleteModule,
	  MatButtonModule,
	  MatButtonToggleModule,
	  MatCardModule,
	  MatCheckboxModule,
	  MatChipsModule,
	  MatDatepickerModule,
	  MatDialogModule,
	  MatExpansionModule,
	  MatGridListModule,
	  MatIconModule,
	  MatInputModule,
	  MatListModule,
	  MatMenuModule,
	  MatNativeDateModule,
	  MatPaginatorModule,
	  MatProgressBarModule,
	  MatProgressSpinnerModule,
	  MatRadioModule,
	  MatRippleModule,
	  MatSelectModule,
	  MatSidenavModule,
	  MatSliderModule,
	  MatSlideToggleModule,
	  MatSnackBarModule,
	  MatSortModule,
	  MatTableModule,
	  MatTabsModule,
	  MatToolbarModule,
	  MatTooltipModule,
	  MatStepperModule,
	} from '@angular/material';
	
const routes: Routes = [
	{
        "path": "",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": TicketListComponent
            }
        ]
    },{
        "path": "ticket-add",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": TicketAddComponent
            }
        ]
    },
	{
        "path": "ticket-dash",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": TicketDashComponent,
				
            }
        ]
    }
	
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes), LayoutModule, HttpClientModule, 
        FormsModule, ReactiveFormsModule, ListModule, ToastModule, SmartSearchModule,
       MatInputModule, MatNativeDateModule, MatCardModule, MatSelectModule, MatSidenavModule, MatTableModule, MatSortModule, MatDatepickerModule, MatCheckboxModule, MatRadioModule, MatPaginatorModule, MatTabsModule
    ],
	exports: [ RouterModule ],
	declarations: [ TicketListComponent, TicketAddComponent, TicketDashComponent ]
})

export class TicketListModule { }




